




























































export default {
	data() {
		return {
			// 选择状态
			status:'day',
			money:0,
			price:0,
			dc_id:'',
			day:0,
			month:0,
			qu:0,
			hyear:0,
			year:0,
			// 遮罩层数据
			maskStatus:false,
			num:false,
		};
	},
	methods:{
	// 遮罩层确定和取消
		maskClick(e){
			var that = this;
			var id = e.currentTarget.dataset.id;
			if(id==1){
				that.maskStatus = !that.maskStatus;
				uni.navigateTo({
					url:'../pw/pw'
				})
			}else if(id==2){
				that.maskStatus = !that.maskStatus;
			}
		},
		change(str){
			if(str=='day'){
				this.status = 'day'
				this.price = Number(this.day)+Number(this.money)
			}else if(str == 'month'){
				this.status = 'month'
				this.price = Number(this.month)+Number(this.money)
			}else if(str == 'qu'){
				this.status = 'qu'
				this.price = Number(this.qu)+Number(this.money)
			}else if(str == 'hyear'){
				this.status = 'hyear'
				this.price = Number(this.hyear)+Number(this.money)
			}else if(str == 'year'){
				this.status = 'year'
				this.price = Number(this.year)+Number(this.money)
			}
		}
	},
	onLoad(option) {
		this.money = option.money
		this.dc_id = option.id
		console.log(option)
		// 请求租用期限
		uni.request({
			url:this.http+'index/getDcInfo',
			method:'POST',
			header:this.header,
			data:{
				dc_id:this.dc_id
			},
			success: (res) => {
				console.log(res)
				this.day = res.data.data.rent_day
				this.month = res.data.data.rent_month
				this.qu = res.data.data.rent_quarter
				this.hyear = res.data.data.rent_byear
				this.year = res.data.data.rent_year
				// 默认租用时间为1天
				this.change('day');
			},
			fail() {
				uni.showToast({
					title:'网络延迟，请刷新后重试!',
					icon:'none'
				})
			}
		})
		
		
	}
}
